@font-face {
  font-style: normal;
  font-weight: 100;
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/roboto/roboto-100.woff2") format("woff2"), url("../fonts/roboto/roboto-100.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/roboto/roboto-300.woff2") format("woff2"), url("../fonts/roboto/roboto-300.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/roboto/roboto-400.woff2") format("woff2"), url("../fonts/roboto/roboto-400.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/roboto/roboto-500.woff2") format("woff2"), url("../fonts/roboto/roboto-500.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/roboto/roboto-700.woff2") format("woff2"), url("../fonts/roboto/roboto-700.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  font-family: "Roboto", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  /* stylelint-disable */
  font-weight: 400 !important;
  /* stylelint-enable */
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 60px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 50px;
  }
}
@media (max-width: 1279px) {
  .container {
    padding: 0 44px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 40px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px 0 21px;
  }
}

.header {
  position: relative;
  padding: 46px 0;
  background-color: #192144;
  z-index: 5;
}
.header::before {
  position: absolute;
  bottom: -2px;
  left: 0;
  right: 0;
  width: 100%;
  height: 4px;
  background-color: #192144;
  content: "";
}
.header .container {
  display: flex;
  flex-wrap: wrap;
}
@media (max-width: 1023px) {
  .header {
    padding: 44px 0;
    z-index: 25;
  }
  .header .container {
    flex-direction: column;
  }
  .header--menu-is-shown {
    background-color: #ffffff;
  }
  .header--menu-is-shown .header__logo > .header__icon {
    display: none;
  }
  .header--menu-is-shown .header__logo > .header__icon-mobile {
    margin-left: -1px;
    display: block;
  }
}
@media (max-width: 767px) {
  .header {
    padding: 18px 0 22px;
  }
  .header::before {
    display: none;
  }
  .header .container {
    padding: 0 25px;
  }
}
.header--index {
  background-color: #516172;
}
.header--index::before {
  display: none;
}
@media (max-width: 1023px) {
  .header--index {
    padding: 44px 0 68px;
  }
  .header--index::before {
    display: none;
  }
  .header--index > .header__nav-wrapper {
    background-color: #516172;
  }
  .header--index.header--menu-is-shown {
    background-color: #ffffff;
  }
}
@media (max-width: 767px) {
  .header--index {
    padding: 36px 0 22px;
  }
}
.header__logo {
  padding-top: 1px;
  width: 202px;
  transition: opacity 0.3s ease;
}
.header__logo > .header__icon-mobile {
  display: none;
  width: 168px;
  height: 42px;
}
.header__logo[href]:hover, .header__logo[href]:focus {
  opacity: 0.8;
}
.header__logo[href]:active {
  opacity: 0.6;
}
@media (max-width: 1023px) {
  .header__logo {
    display: block;
  }
}
@media (max-width: 767px) {
  .header__logo {
    padding-left: 1px;
  }
  .header__logo > .header__icon-mobile {
    width: 96px;
    height: 15px;
  }
}
@media (max-width: 374px) {
  .header__logo {
    width: 100px;
  }
}
.header__nav {
  position: relative;
  padding-left: 143px;
  width: calc(100% - 202px);
}
.header__nav::before {
  position: absolute;
  top: 28px;
  left: 38px;
  width: 60px;
  height: 2px;
  background-color: #ffffff;
  content: "";
}
@media (max-width: 1439px) {
  .header__nav {
    width: calc(100% - 206px);
  }
}
@media (max-width: 1023px) {
  .header__nav {
    padding-left: 0;
    width: 100%;
  }
  .header__nav::before {
    display: none;
  }
}
.header__nav-toggle {
  position: relative;
  width: 25px;
  height: 25px;
  display: none;
  background-color: rgba(255, 255, 255, 0);
  border: none;
  appearance: none;
  cursor: pointer;
}
@media (max-width: 1023px) {
  .header__nav-toggle {
    position: absolute;
    top: -32px;
    right: 0;
    display: block;
  }
  .header__nav-toggle--is-open > .header__nav-toggle-icon--menu-is-hidden {
    display: none;
  }
  .header__nav-toggle--is-open > .header__nav-toggle-icon--menu-is-shown {
    display: block;
    color: #cccccc;
  }
}
@media (max-width: 767px) {
  .header__nav-toggle {
    top: -18px;
    right: 1px;
    left: auto;
    margin: 0;
  }
  .header__nav-toggle--is-open {
    top: -17px;
    right: -3px;
  }
  .header__nav-toggle--is-open > .header__nav-toggle-icon--menu-is-shown {
    width: 20px;
    height: 20px;
  }
  .header__nav-toggle > .header__nav-toggle-icon--menu-is-shown > svg {
    width: 100%;
    height: 100%;
  }
}
.header__nav-toggle-icon {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}
.header__nav-toggle-icon--menu-is-shown {
  display: none;
}
.header__nav-wrapper {
  gap: 20px;
  display: grid;
  grid-template-columns: minmax(max-content, 715px) minmax(200px, 240px);
}
@media (max-width: 1439px) {
  .header__nav-wrapper {
    grid-template-columns: minmax(min-content, 1fr) minmax(min-content, auto);
  }
}
@media (max-width: 1023px) {
  .header__nav-wrapper {
    position: absolute;
    top: 0;
    left: -40px;
    right: 0;
    padding: 54px 40px 80px;
    display: none;
    grid-template-columns: 100%;
    gap: 32px;
    width: calc(100% + 80px);
    background-color: #ffffff;
    border-radius: 0 0 38px 38px;
    z-index: 15;
  }
  .header__nav-wrapper--menu-is-shown {
    display: grid;
  }
  .header__nav-wrapper::before {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 40px;
    background-color: #121936;
    border-radius: 0 0 38px 38px;
    content: "";
  }
}
@media (max-width: 767px) {
  .header__nav-wrapper {
    left: -25px;
    right: -25px;
    padding: 54px 25px 48px;
    width: calc(100% + 50px);
    gap: 19px;
    border-radius: 0 0 22px 22px;
    box-shadow: none;
  }
  .header__nav-wrapper::before {
    height: 19px;
    border-radius: 0 0 22px 22px;
  }
}
.header__nav-list {
  margin: 0;
  padding: 21px 0 12px;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  gap: 18px;
