    width: 100%;
  }
}
.header__nav-link {
  position: relative;
  display: block;
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
  color: #ffffff;
  text-transform: uppercase;
  transform: translate3d(0, 0, 0);
  transition: transform 0.3s ease;
}
.header__nav-link::before {
  position: absolute;
  top: 100%;
  content: attr(data-hover);
}
.header__nav-link[href]:hover, .header__nav-link[href]:focus {
  color: #7ebaeb;
  transform: translateY(-100%);
  transition: transform 0.3s ease;
}
.header__nav-link[href]:active {
  color: rgba(78, 145, 209, 0.3);
}
@media (max-width: 1023px) {
  .header__nav-link {
    padding: 9px 0;
    font-size: 28px;
    line-height: 28px;
    color: #192144;
  }
  .header__nav-link::before {
    display: none;
  }
  .header__nav-link[href]:hover, .header__nav-link[href]:focus {
    color: #192144;
    transform: translateY(0);
  }
  .header__nav-link[href]:active {
    opacity: 0.6;
  }
}
@media (max-width: 767px) {
  .header__nav-link {
    padding: 0;
    width: 100%;
    font-size: 30px;
    line-height: 43px;
  }
}
