  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
  pointer-events: all;
  cursor: auto;
}

.input-upload--file-drop .input-upload__preview-icon {
  position: absolute;
  z-index: 1;
  width: 30px;
  height: auto;
  left: 10px;
  top: 10px;
}

.input-upload--file-drop .input-upload__preview-file-info {
  position: absolute;
  width: 100%;
  height: 40px;
  background-color: rgba(0, 0, 0, 0.8);
  color: #ffffff;
  padding: 5px 10px;
  z-index: 1;
  left: 0;
  bottom: 0;
}

